          @import "src/general/_fonts.scss";
        
.add-new-book-modal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background: $dark-background;
  z-index: 999;

  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 40%;
    height: max-content;
    padding: 0 160px 50px;
    background-color: $white;
    border-radius: 24px;
  }

  &__title {
    font-family: "Montserrat Medium";
    font-size: 25px;
    margin-top: 75px;
  }
}

.add-new-book-modal-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-top: 35px;

  &__row {
    width: 100%;
    margin-bottom: 25px;
  }
}
