.confirm-delete-modal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background: $dark-background;
  z-index: 999;

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 40%;
    height: 35vh;
    padding: 0 160px;
    background-color: $white;
    border-radius: 24px;
  }

  &__title {
    font-family: "Montserrat Bold";
    font-size: 30px;
    text-align: center;
  }

  &__book-name {
    color: $blue;
  }

  &__buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-top: 45px;
  }

  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 35%;
    height: 70px;
    margin-left: 30px;
    font-family: "Montserrat Bold";
    font-size: 18px;
    background-color: $light;
    border: 0;
    border-radius: 24px;
    cursor: pointer;
    transition: box-shadow 0.2s;

    &:hover {
      box-shadow: 9px 13px 31px -6px rgba(0, 0, 0, 0.3);
    }
  }
}

.confirm {
  margin-left: 0;
  background-color: $red;
  color: $white;
}
