.book-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 30%;
  margin-top: 50px;

  &__row {
    width: 100%;
    margin-bottom: 25px;
  }

  &__submit {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 300px;
    height: 60px;
    margin-top: 25px;
    font-family: "Montserrat SemiBold";
    font-size: 17px;
    background-color: $blue;
    color: $white;
    border: 0;
    border-radius: 24px;
    cursor: pointer;
    transition: background-color 0.2s;

    &:hover {
      background-color: $second-blue;
    }

    &:disabled {
      opacity: 0.5;
      cursor: not-allowed;

      &:hover {
        background-color: $blue;
      }
    }
  }
}
