          @import "src/general/_fonts.scss";
        
.book-edit {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100vh;

  &__title {
    margin-top: 100px;
    font-family: "Montserrat SemiBold";
  }

  &__button {
    position: absolute;
    top: 50px;
    left: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 140px;
    height: 50px;
    font-family: "Montserrat SemiBold";
    font-size: 17px;
    background-color: $blue;
    color: $white;
    border: 0;
    border-radius: 24px;
    text-decoration: none;
    cursor: pointer;
    transition: 0.2s;

    &:hover {
      background-color: $second-blue;
    }
  }
}
